}
.about__btn::after {
  content: "";
  position: absolute;
  margin: auto;
  bottom: -0.1875rem;
  left: 0;
  height: 0.125rem;
  width: 0;
  background-color: var(--very-dark);
  transition: width 0.3s cubic-bezier(0.33, 1, 0.68, 1);
  width: 40%;
}
.about__btn:hover::after {
