}
@media (min-width: 64em) {
  .cta__text {
    margin-bottom: 0;
    margin-right: 1.625rem;
  }
}
.cta__btn {
  margin: 0;
}
.cta::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.35);
