.feedback__scroll-circle {
  display: inline-block;
  height: 0.5625rem;
  width: 0.5625rem;
  border: 1px solid var(--very-dark);
  border-radius: 50%;
  transition: background-color 0.2s ease-in;
}
.feedback__scroll-circle:not(:last-of-type) {
  margin-right: 0.5rem;
}
.feedback__scroll-circle.active {
  background-color: var(--very-dark);
}
.feedback__scroll-circle:hover {
  cursor: pointer;
}

.blog {
  padding: 3rem 0;
  background-color: var(--light-gray);
  text-align: center;
}
.blog__heading {
  margin-bottom: 2.25rem;
}
.blog__container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1.5rem;
}
.blog__card {
  position: relative;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.blog__card:hover {
  cursor: pointer;
}
.blog__card:hover > :first-child::after {
  transform: scale(1.05);
}
.blog__card-image {
